/*eslint-disable */
const core = chrome || browser;
/*eslint-enable */

export const mpToContent = async (message, params) => {
  console.log("Pericles.mp.mpToContent(message, params)", message, params);
  return new Promise(async (resolve, reject) => {
    await core.tabs.query(
      {
        currentWindow: true,
        active: true
      },
      async tabs => {
        const activeTab = tabs[0];
        if (activeTab && activeTab.id) {
          const sentData = {};
          sentData.message = message;
          if (typeof params !== "undefined") sentData.params = params;
          console.log(
            "Pericles.mp.mpToContent(activeTab, sentData)",
            activeTab,
            sentData
          );
          core.tabs.sendMessage(activeTab.id, sentData, async data => {
            console.log("Pericles.mp.mpToContent.response(data)", data);
            resolve({ tabId: activeTab.id, response: data || true });
          });
        } else {
          reject({ error: "no active tab", response: false });
        }
      }
    );
  });
};

export const getContentTabId = async () => {
  console.log("Pericles.mp.getContentTabId");
  return new Promise(async (resolve, reject) => {
    core.tabs.query(
      {
        currentWindow: true,
        active: true
      },
      async tabs => {
        const activeTab = tabs[0];
        resolve(activeTab.id);
      }
    );
  });
};

export const mpToBackground = async (message, params) => {
  console.log("Pericles.mp.mpToBackground(message, params)", message, params);
  return new Promise((resolve, reject) => {
    const sentData = {};
    sentData.message = message;
    if (typeof params !== "undefined") sentData.params = params;

    core.runtime.sendMessage(sentData, response => {
      console.log("Pericles.mp.mpToBackground.sendMessage.response", response);
      resolve(response);
    });
  });
};

